@use '@styles/main.scss';
@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/mixins' as mixin;

.home {
  &__banner:before {
    background-image: url('@images/home/banner.jpg');
  }
}

.direction {
  position: relative;
  padding-top: 28.6rem;

  @include mixin.laptop { padding-top: 24.6rem; }
  @include mixin.wide-tablet { padding-top: 20.6rem; }
  @include mixin.tablet { padding-top: 16.6rem; }
  @include mixin.small-tablet { padding-top: 12.6rem; }
  @include mixin.sm-phone { padding-top: 8rem; }

  &__title {
    margin-bottom: 5rem;

    @include mixin.laptop { margin-bottom: 4.4rem; }
    @include mixin.wide-tablet { margin-bottom: 3.8rem; }
    @include mixin.tablet { margin-bottom: 3.2rem; }
    @include mixin.small-tablet { margin-bottom: 2.6rem; }
    @include mixin.sm-phone { margin-bottom: 2rem; }
  }

  &__image {
    width: 100%;
    max-width: 33.124rem;
    margin-bottom: 5.8rem;
    
    @include mixin.laptop { margin-bottom: 5.4rem; }
    @include mixin.wide-tablet { margin-bottom: 42.8rem; }
    @include mixin.tablet { width: 80%; margin-bottom: 3.52rem; }
    @include mixin.small-tablet { margin-bottom: 2.76rem; }
    @include mixin.sm-phone { width: 70%; margin-bottom: 2rem; }
  }

  &__hr {
    position: absolute;
    left: 0;
    width: 60%;
    margin: 0;
    height: 1px;
    background: linear-gradient(to right, var.$color4, rgba(var.$color4, 0));

    @include mixin.small-tablet { display: none; }
  }

  &__companies {
    padding-top: 7.7rem;
    display: flex;
    align-items: center;
    gap: 4.5rem;
    max-width: 560px;

    @include mixin.laptop { padding-top: 6.84rem; }
    @include mixin.wide-tablet { padding-top: 5.88rem; gap: 3.5rem; }
    @include mixin.tablet { padding-top: 4.92rem; }
    @include mixin.small-tablet { padding-top: 3.96rem; gap: 2.5rem; }
    @include mixin.sm-phone { padding-top: 3rem; flex-direction: column; }

    a {
      flex-grow: 1;

      img {
        width: 100%;
      }
    }
  }

  &__banner {
    position: absolute;
    top: -6%;
    left: 45%;
    width: 75.83%;
    height: auto;
    pointer-events: none;
    z-index: -1;

    @include mixin.small-tablet { display: none; }
  }
}

.clinics {
  padding-top: 24.8rem;

  @include mixin.laptop { padding-top: 22rem; }
  @include mixin.wide-tablet { padding-top: 18.5rem; }
  @include mixin.tablet { padding-top: 15rem; }
  @include mixin.small-tablet { padding-top: 11.5rem; }
  @include mixin.sm-phone { padding-top: 8rem; }

  &__title {
    margin-bottom: 7rem;

    @include mixin.laptop { margin-bottom: 6.2rem; }
    @include mixin.wide-tablet { margin-bottom: 5.4rem; }
    @include mixin.tablet { margin-bottom: 4.6rem; }
    @include mixin.small-tablet { margin-bottom: 3.8rem; }
    @include mixin.sm-phone { margin-bottom: 3rem; }

    a {
      display: flex;
      align-items: center;
      gap: 4rem;

      @include mixin.tablet { gap: 2.5rem; }
      @include mixin.sm-phone { gap: 1rem; }
    }

    span {
      color: var.$color2;
    }
  }

  .clinic {
    position: relative;
    display: block;
    width: 100%;
    height: 61rem;
    box-sizing: border-box;
    padding: 5.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    border-radius: 10px;
    overflow: hidden;

    @include mixin.laptop { height: 54rem; padding: 4.8rem; }
    @include mixin.wide-tablet { height: 48rem; padding: 4.2rem; }
    @include mixin.tablet { height: 42rem; padding: 3.6rem; }
    @include mixin.small-tablet { height: 36rem; padding: 3rem; }
    @include mixin.sm-phone { height: 30rem; padding: 2.4rem; }

    &__content {
      position: relative;
    }

    &__image {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      object-fit: cover;
      z-index: -1;
    }

    &__title {
      font-size: 3.2rem;
      font-weight: 800;
      line-height: 1.2;
      color: var.$color1;

      @include mixin.laptop { font-size: 2.92rem; }
      @include mixin.wide-tablet { font-size: 2.64rem; }
      @include mixin.tablet { font-size: 2.36rem; }
      @include mixin.small-tablet { font-size: 2.08rem; }
      @include mixin.sm-phone { font-size: 1.8rem; }
    }

    &__swiper-control {
      margin-top: 7rem;
      display: flex;
      align-items: center;

      @include mixin.laptop { margin-top: 6.2rem; }
      @include mixin.wide-tablet { margin-top: 5.4rem; }
      @include mixin.tablet { margin-top: 4.6rem; }
      @include mixin.small-tablet { margin-top: 3.8rem; }
      @include mixin.sm-phone { margin-top: 3rem; }

      svg {
        width: 3.2rem;
        height: 3.2rem;
        cursor: pointer;
        stroke: var.$color5;
        transition: 0.3s linear;

        &:hover {
          stroke: var.$color3;
        }
      }
    }
  }
}

.request {
  position: relative;
  padding-top: 12.9rem;
    
  @include mixin.laptop { padding-top: 11.75rem; }
  @include mixin.wide-tablet { padding-top: 10.6rem; }
  @include mixin.tablet { padding-top: 9.4rem; }
  @include mixin.small-tablet { padding-top: 8.2rem; }
  @include mixin.sm-phone { padding-top: 7rem; }

  &__title {
    margin-bottom: 3.8rem;
    
    @include mixin.tablet { margin-bottom: 2.9rem; }
    @include mixin.sm-phone { margin-bottom: 2rem; }
  }

  .form {
    width: 100%;
    max-width: 591px;

    &__title {
      font-size: 3.2rem;
      line-height: 1.41;
      font-weight: 400;
      margin-bottom: 5rem;
    
      @include mixin.laptop { font-size: 2.8rem; margin-bottom: 4.2rem; }
      @include mixin.wide-tablet { font-size: 2.55rem; margin-bottom: 3.6rem; }
      @include mixin.tablet { font-size: 2.3rem; margin-bottom: 3.2rem; }
      @include mixin.small-tablet { font-size: 2.05rem; margin-bottom: 2.6rem; }
      @include mixin.sm-phone { font-size: 1.8rem; margin-bottom: 2rem; }
    }

    &__input {
      width: 100%;
      border: none;
      outline: none;
      border-radius: 1rem;
      box-sizing: border-box;
      padding: 2.6rem;
      font-family: var.$font-family2;
      font-size: 2rem;
      font-weight: 400;
      line-height: 1;
      margin-bottom: 1.5rem;
      background: transparent;
      box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
    
      @include mixin.wide-tablet { padding: 2rem; font-size: 1.8rem; margin-bottom: 1.25rem; }
      @include mixin.small-tablet { padding: 1.4rem; font-size: 1.6rem; margin-bottom: 1rem; }

      &::placeholder {
        font-family: var.$font-family2;
        font-size: 2rem;
        font-weight: 400;
        line-height: 1;
        color: var.$color6;
        
        @include mixin.wide-tablet { font-size: 1.8rem; }
        @include mixin.small-tablet { font-size: 1.6rem; }
      }

      &--last {
        margin-bottom: 3rem;

        @include mixin.wide-tablet { margin-bottom: 2.25rem; }
        @include mixin.small-tablet { margin-bottom: 1.5rem; }
      }
    }

    &__checkbox {
      display: flex;
      align-items: center;
      gap: 1.2rem;
      margin-bottom: 1rem;
      font-family: var.$font-family2;
      font-size: 1.4rem;
      font-weight: 400;
      line-height: 2rem;
      color: var.$color7;
      cursor: pointer;
      
      div {
        width: 1.7rem;
        height: 1.7rem;
        box-sizing: border-box;
        border-radius: 0.5rem;
        background: transparent;
        border: 1px solid var.$color7;
        cursor: pointer;

        input {
          position: absolute;
          opacity: 0;
          cursor: pointer;
        }
      }

      &--last {
        margin-bottom: 4rem;
      }

      a {
        color: var.$color3;
        gap: 0;
        margin: 0;
      }
    }

    &__button {
      background: var.$color3;
      border-radius: 1rem;
      padding: 2.55rem 3rem;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      gap: 1rem;
      font-size: 2rem;
      color: var.$color1;
      font-weight: 500;
      font-family: var.$font-family;

      @include mixin.wide-tablet { padding: 2rem 2.4rem; font-size: 1.8rem; }
      @include mixin.small-tablet { padding: 1.5rem 1.8rem; font-size: 1.6rem; }
    }
  }

  &__banner {
    position: absolute;
    top: -28%;
    left: 54%;
    z-index: -1;
    width: 67.8%;

    @include mixin.small-tablet { display: none; }
  }
}

.news {
  padding-top: 24rem;
    
  @include mixin.laptop { padding-top: 20.8rem; }
  @include mixin.wide-tablet { padding-top: 17.6rem; }
  @include mixin.tablet { padding-top: 14.4rem; }
  @include mixin.small-tablet { padding-top: 11.2rem; }
  @include mixin.sm-phone { padding-top: 8rem; }

  &__title {
    margin-bottom: 7rem;

    @include mixin.laptop { margin-bottom: 6.2rem; }
    @include mixin.wide-tablet { margin-bottom: 5.4rem; }
    @include mixin.tablet { margin-bottom: 4.6rem; }
    @include mixin.small-tablet { margin-bottom: 3.8rem; }
    @include mixin.sm-phone { margin-bottom: 3rem; }

    a {
      display: flex;
      align-items: center;
      gap: 4rem;

      @include mixin.tablet { gap: 2.5rem; }
      @include mixin.sm-phone { gap: 1rem; }
    }

    span {
      color: var.$color2;
    }
  }

  .news-item {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2.9rem;
    margin-bottom: 3rem;

    @include mixin.tablet { margin-bottom: 2.2rem; gap: 1.95rem; }
    @include mixin.small-tablet { margin-bottom: 1.4rem; flex-direction: column; gap: 1rem; }

    &__image {
      width: 30%;
      height: 100%;
      object-fit: contain;

      @include mixin.small-tablet { width: 80%; }
    }

    &__content {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }

    &__date {
      font-size: 1.4rem;
      line-height: 2.4rem;
      font-weight: 400;
      color: var.$color4;
      margin-bottom: 1.6rem;

      @include mixin.tablet { margin-bottom: 1.4rem; }
      @include mixin.small-tablet { margin-bottom: 1.2rem; }
    }

    &__title {
      font-size: 2.4rem;
      line-height: 1;
      font-weight: bold;
      color: var.$color2;
      margin-bottom: 2rem;
      transition: 0.3s linear;

      @include mixin.tablet { font-size: 2.1rem; margin-bottom: 1.5rem; }
      @include mixin.small-tablet { font-size: 1.8rem; margin-bottom: 1rem; }

      &:hover,
      &:visited {
        color: var.$color3;
      }
    }

    &__text {
      font-size: 1.6rem;
      line-height: 1.5;
      margin-bottom: 1rem;
    }
  }
}

.location {
  padding-top: 10rem;

  @include mixin.laptop { padding-top: 9rem; }
  @include mixin.wide-tablet { padding-top: 8rem; }
  @include mixin.tablet { padding-top: 7rem; }
  @include mixin.small-tablet { padding-top: 6rem; }
  @include mixin.sm-phone { padding-top: 5rem; }

  &__title {
    margin-bottom: 6rem;

    @include mixin.wide-tablet { margin-bottom: 4.5rem; }
    @include mixin.small-tablet { margin-bottom: 3rem; }
  }

  &__map {
    width: 100%;
    aspect-ratio: 2.6;

    @include mixin.wide-tablet { aspect-ratio: 1.5; }
    @include mixin.small-tablet { aspect-ratio: 1; }
  }
}